$breakpoint-tablet: 720px;
.home {
  .header {
    background: rgb(211, 47, 47);
    padding-top: 60px;
    color: #fff;
    .headerWrapper {
      width: 570px;
      @media (max-width: $breakpoint-tablet) {
        width: 100%;
      }
      margin: 0 auto;
      box-shadow: 0 -5px 40px 7px rgba(0, 0, 0, 0.08);
      .mainHeader {
        align-items: center;
        border-bottom: 1px solid rgba(255, 255, 255, 0.15);
        display: flex;

        @media (max-width: $breakpoint-tablet) {
          flex-direction: column;
        }
        .comLogo {
          width: 300px;
          height: auto;
        }
      }
      .headerBtn {
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        .numLink {
          text-decoration: none;
          color: #fff;
          width: 100%;
          .callBtn {
            padding: 10px 0;
            border-right: 1px solid rgba(255, 255, 255, 0.15);
            &:hover {
              background: rgba(0, 0, 0, 0.1);
            }
            .callIcon {
              color: #fff;
            }
            .paraText {
              margin: 0;
              display: block;
              text-transform: uppercase;
              font-size: 10px;
            }
          }
        }
        .mailLink {
          text-decoration: none;
          color: #fff;
          width: 100%;
          .mailBtn {
            cursor: pointer;
            padding: 10px 0;
            &:hover {
              background: rgba(0, 0, 0, 0.1);
            }
            .emailIcon {
              color: #fff;
            }
            .paraText {
              margin: 0;
              display: block;
              text-transform: uppercase;
              font-size: 10px;
            }
          }
        }
      }
    }
  }
  .cardContainer {
    height: 100%;
    position: relative;
    width: 570px;
    @media (max-width: $breakpoint-tablet) {
      width: 100%;
    }
    margin: 0 auto;
    box-shadow: 0 -5px 40px 7px rgba(0, 0, 0, 0.08);
    margin-bottom: 60px;
    overflow: hidden;
    .card {
      padding: 20px;
      display: flex;
      align-items: start;
      flex-direction: column;
      .vCardProfile {
        display: flex;
        gap: 30px;
        width: 100%;
        padding-top: 20px;
        .numberImg {
          display: flex;
          align-items: center;
        }
        .number {
          display: flex;
          flex-direction: column;
          width: 100%;
          align-items: flex-start;
          border-bottom: 1px solid #eaeaea;
          padding-bottom: 10px;
          .numberLink {
            transition: all 0.15s ease;
            outline: none;
            text-decoration: none;
            color: #323032;
            margin-bottom: 5px;
          }
          .numberText {
            display: block;
            color: #82848f;
            font-size: 13px;
            margin: 0;
          }
        }
        .saveBtn {
          background: #d3302f;
          outline: none;
          border: none;
          color: #fff;
          box-shadow: 0px 10px 14.1px 0.9px rgba(0, 0, 0, 0.24), 0px 4px 19.6px 0.4px rgba(0, 0, 0, 0.16);
          height: 56px;
          border-radius: 2px;
          width: 300px;
          display: flex;
          gap: 20px;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          text-transform: uppercase;
          transition: 0.1s ease-in;
          &:hover {
            box-shadow: 0px 20px 28.2px 0.9px rgba(0, 0, 0, 0.24), 0px 8px 40.18px 0.82px rgba(0, 0, 0, 0.16);
          }
        }
        .saveContactModal {
          position: fixed;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background: rgba(0, 0, 0, 0.3);
          display: flex;
          align-items: center;
          justify-content: center;
          .backDrop {
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: rgba(0, 0, 0, 0.3);
            z-index: 9;
          }
          .modalBox {
            z-index: 10;
            padding: 20px;
            box-shadow: 0px 4px 7.36px 0.64px rgba(0, 0, 0, 0.24), 0px 1px 7.68px 0.32px rgba(0, 0, 0, 0.16);
            background: #fff;
            position: relative;
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            gap: 5px;
            .modalHeader {
              color: rgb(239, 154, 154);
              margin-bottom: 5px;
              font-size: 14px;
              font-weight: 500;
            }
            .modalSubHeader {
              margin-bottom: 5px;
              font-size: 14px;
              color: rgba(0, 0, 0, 0.87);
              font-weight: 500;
            }
            .contactAction {
              text-decoration: none;
              list-style: none;
              padding: 0;
              .addContactAction {
                display: flex;
                cursor: pointer;
                align-items: center;
                justify-content: center;
                gap: 12px;
              }
            }
            .CloseBtn {
              position: absolute;
              top: 8px;
              right: 12px;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
